




































































































































@import '@design';

.player {
  &__turntable {
    display: flex;
    padding: 15px 25px;
    justify-content: space-between;
  }
  &__controller {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__panel {
    position: relative;
    flex: 1;
    padding: 15px 25px;
    display: flex;
    flex-direction: column;
    &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 30px;
    }
  }
  &__spinning {
    position: absolute;
    transform: translate(25px, calc(-100% + 73px));
    font-size: 0;
    .button {
      position: absolute;
      right: 2px;
      bottom: 5px;
      width: 88px;
      height: 88px;
      margin-bottom: 0;
      &__icon {
        width: 40%;
      }
    }
    .spinning__img {
      pointer-events: none;
      transition: transform $duration-transition-base linear;
      transform: rotate(-15deg);
      transform-origin: calc(100% - 50px) calc(100% - 50px);
      will-change: transform;
    }
  }
  &__volumn {
    display: flex;
    .volume__icon {
      width: 1em;
    }
  }
  &--playing {
    .disk {
      animation-play-state: running;
    }
    .spinning__img {
      transform: rotate(0);
    }
  }
}

.disk {
  flex-basis: 80%;
  position: relative;
  margin-left: 25px;
  animation: spinning $duration-animation-base infinite linear;
  animation-play-state: paused;
  &__img {
    border-radius: 50%;
    border: 3px solid $color-primary;
    width: 100%;
    box-shadow: $color-box-shadow;
  }
  &__hole {
    position: absolute;
    width: 100px;
    height: 100px;
    background: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    border: 3px solid $color-primary;
    box-shadow: inset $color-box-shadow;
  }
  &__effect {
    position: absolute;
    animation: shake $duration-animation-base * 2 infinite linear;
    &--left {
      top: 18%;
      left: 6%;
    }
    &--right {
      top: 35%;
      right: 13%;
    }
  }
}
.playing {
  &__progress {
    display: flex;
    align-items: center;
  }
}

@keyframes spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes shake {
  from,
  to {
    transform: translate3d(0, 0, 0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translate3d(0, -10px, 0);
  }

  20%,
  40%,
  60%,
  80% {
    transform: translate3d(0, 10px, 0);
  }
}
