

































































































$material-icons-font-path: '~material-icons/iconfont/';
@import '~material-icons/iconfont/material-icons.scss';

@import '@design';
@import '@src/design/reset.scss';
#app {
  font-family: $system-default-font-family;
}
.api-tester {
  position: absolute;
  right: 0;
  top: 50%;
}
.copyright {
  margin-top: 30px;
  color: $color-grey;
}
.box {
  width: 90vh;
  height: 90vh;
  margin: 5vh auto;
  background-color: white;
  border: 3px solid $color-primary;
  position: relative;
  border-radius: 4px;
  transition: transform $duration-transition-base linear;
  will-change: transform;
  display: flex;
  flex-direction: column;
  transform: translateX(0);
  &__side {
    content: '';
    border: 3px solid $color-primary;
    background-color: #c5c5c5;
    position: absolute;
    transform-origin: top left;
    border-radius: 4px;
    transition: all $duration-transition-base * 2 linear;
    box-sizing: content-box;
    &--top,
    &--bottom {
      width: 100%;
      height: 15px;
    }
    &--left,
    &--right {
      width: 15px;
      height: 100%;
    }

    &--top {
      transform: skewX(45deg) rotateX(180deg);
      top: 0px;
      left: -1px;
      opacity: 1;
    }
    &--bottom {
      transform: skewX(45deg) rotateX(180deg);
      bottom: -24px;
      left: -1px;
      box-shadow: 3px 0px 6px rgba(0, 0, 0, 0.2);
    }
    &--left {
      transform: skewY(45deg) rotateY(180deg);
      top: -2px;
      left: 1px;
      opacity: 1;
    }
    &--right {
      transform: skewY(45deg) rotateY(180deg);
      top: -2px;
      right: -24px;
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
    }
  }
  &--open {
    .box__side {
      &--top {
        transform: skewX(45deg);
        left: -4px;
        top: -3px;
        opacity: 0;
      }
      &--bottom {
        transform: skewX(45deg);
        bottom: -21px;
        left: -4px;
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
      }

      &--left {
        transform: skewY(45deg);
        left: -3px;
        top: -4px;
        opacity: 0;
      }
      &--right {
        transform: skewY(45deg);
        right: -21px;
        top: -4px;
        box-shadow: 3px 0px 6px rgba(0, 0, 0, 0.2);
      }
    }
  }
}
